const lessonNumbers = "https://via.placeholder.com/300x200?text=Numbers";
const lessonEmotions = "https://via.placeholder.com/300x200?text=Emotions";

// Header Component
const Header = () => {
  return (
    <header className="bg-background/95 backdrop-blur-sm border-b sticky top-0 z-50">
      <div className="container mx-auto px-4 py-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-3">
            <div className="w-10 h-10 bg-primary rounded-xl flex items-center justify-center">
              <span className="text-primary-foreground font-bold text-lg">GL</span>
            </div>
            <span className="text-2xl font-bold text-foreground">GoLearn</span>
          </div>
          <Button variant="ghost" size="sm">
            Profile
          </Button>
        </div>
      </div>
    </header>
  );
};

// Progress Stats Component
